/*
================================================================================
|                                     FOOTER                                   |
================================================================================
*/
footer {
  min-height: 179px;
  text-align: left;
  width: 100%;
  min-width: $min-width;
  order: 3;
  -webkit-order: 3;
  font-family: "Open Sans", sans-serif;
}

.footer__left-column {
  width: 361px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: flex-start;
  padding-left: 60px;
  padding-right: 30px;

  p {
    font-style: normal;
    font-weight: normal;
    font-size: 12px;
    line-height: 15px;
  }
}

.footer__left-column_icon-container {
  margin-top: 20px;
  margin-bottom: 15px;
  a {
    margin-right: 10px;
  }
}

.footer__right-column {
  display: flex;
  flex-grow: 1;
  background: #4b5464 no-repeat;
  background-image: url("../img/flower.png");
  background-position: bottom right;
  padding: 0 65px;

  h5 {
    color: #fff;
  }
}

.footer__right-column_navigate {
  padding-right: 65px;
  width: 500px;
  nav {
    ul {
      display: flex;
      flex-flow: row wrap;
      align-items: center;
      border-right: 0.4px solid #ffffff;

      .footer__right-column_navigate_item {
        width: 50%;
        line-height: 1.5;
        font-size: 14px;
        color: #fff;
        padding-left: 10px;
        padding-right: 10px;
        position: relative;
        font-weight: lighter;

        a {
          color: #fff;

          &:hover {
            color: $green;
          }
        }

        &::before {
          content: url("../img/Arrow.png");
          position: absolute;
          left: 0;
        }

        &.current {
          a {
            color: $green;
          }
        }
      }
    }
  }
}

.footer__right-column_contacts_container {
  display: flex;
  flex-direction: column;
  align-items: flex-start;

  a {
    color: #fff;
    font-style: normal;
    font-weight: lighter;
    font-size: 14px;
    line-height: 1.5;
    margin-bottom: 11px;
    position: relative;
    padding-left: 22px;

    &:hover {
      color: $green;
    }

    &:nth-of-type(1)::before {
      content: "";
      position: absolute;
      width: 16px;
      height: 16px;
      left: 0;
      top: 3px;
      background: url("../img/phone.png") no-repeat;
      background-size: cover;
      background-position: center center;
    }

    &:nth-of-type(2)::before {
      content: "";
      position: absolute;
      width: 16px;
      height: 16px;
      left: 0;
      top: 3px;
      background: url("../img/mailFooter.svg") no-repeat;
      background-size: 100%;
      background-position: center center;
    }
  }
}

@media screen and (max-width: 1200px) {
  .footer__right-column {
    flex-direction: column;
    justify-content: space-between;
    padding: 0 35px;
  }
  .footer__right-column_navigate {
    width: 100%;
    nav {
      ul {
        border-right: none;
      }
    }
  }

  .footer__left-column {
    min-width: 315px;
  }
}

@media screen and (max-width: 991px) {
  .footer__right-column {
    background-image: url("../img/indoorFooter.png");
  }

  .footer__right-column_navigate {
    padding-right: 0;
    nav {
      ul {
        justify-content: space-between;
      }
    }
  }
}

@media screen and (max-width: 767px) {
  footer {
    .wrapper {
      flex-direction: column;
      justify-content: center;
      align-items: center;
    }
  }

  .footer__right-column {
    padding: 0 18px;
  }

  .footer__right-column_navigate {
    width: 100%;
    nav {
      ul {
        align-items: flex-start;
      }
    }
  }

  .footer__left-column {
    width: auto;
    padding: 0 15px;
    align-items: center;
    text-align: center;
  }

  .footer__left-column_icon-container {
    margin: 30px 0;
  }
}
