//main styles
body {
  color: #6e788d;
  font-family: "Open Sans", "Muli", sans-serif;
}

* {
  box-sizing: border-box;
}

.wrapper {
  max-width: 1280px;
  margin: auto;
  display: flex;
  justify-content: space-between;
}

.main-wrapper {
  padding: 0 0 0 0;
  min-width: $min-width;
  max-width: 1280px;
  margin: 0 auto;
  position: relative;
  min-height: 100%;
  display: -webkit-box;
  display: -moz-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  flex-direction: column;
  -webkit-flex-direction: column;
  -ms-flex-direction: column;
  justify-content: flex-start;
  -webkit-justify-content: flex-start;
  -ms-flex-pack: start;
}

.project_btn {
  width: 226px;
  height: 41px;
  font-size: 18px;
  line-height: 21px;
  color: #ffffff;
  background: $green;
  border: none;
  outline: none;
  border-radius: 500px;
  line-height: 21px;
  font-weight: 600;
  margin: 30px auto;
  margin-bottom: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  -moz-user-select: none;
  -khtml-user-select: none;
  -webkit-user-select: none;
  user-select: none;
  position: relative;

  .project_btn_text {
    color: #ffffff;
    border-radius: 50px;
    z-index: 2;
    font-size: 18px;
    text-transform: uppercase;
    font-style: normal;
    font-weight: 600;
    font-size: 14px;
    line-height: 18px;
  }

  &::before {
    content: "";
    position: absolute;
    background: #ffffff;
    width: 0;
    height: 0;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    z-index: 1;
    border-radius: 70px;
    @include transition(0.5s);
  }

  &::after {
    content: "";
    position: absolute;
    background: #ffffff;
    width: 17px;
    height: 1px;
    right: -11px;
    top: 50%;
    transform: translateX(-50%);
    z-index: 1;
    @include transition(0.5s);
  }

  &:hover::before {
    width: 222px;
    height: 37px;
    @include transition(0.5s);
  }

  &:hover {
    .project_btn_text {
      color: $green;
      @include transition(0.5s);
    }
  }
}

/* titles */
p {
  padding: 15px 0 15px 0;
  font-weight: 400;
}
h1 {
  font-style: normal;
  font-weight: 600;
  font-size: 36px;
  line-height: 1.5;
  text-align: center;
  letter-spacing: 0.025em;
  color: #fff;
}
h2 {
  padding: 15px 0 15px 0;
  font-weight: 400;
}
h3 {
  font-style: normal;
  font-size: 25px;
  line-height: 1.5;
  text-align: center;
  letter-spacing: 0.025em;
  color: #fff;
  font-weight: 300;
}

h4 {
  margin: 30px 0;
  font-style: normal;
  font-weight: 600;
  font-size: 20px;
  line-height: 25px;
  text-align: center;
  letter-spacing: 0.045em;
  text-transform: uppercase;
  font-weight: 700;
}
h5 {
  font-style: normal;
  font-weight: 600;
  font-size: 14px;
  line-height: 18px;
  margin: 23px auto;
  text-transform: uppercase;
  letter-spacing: 0.045em;
}
li {
  list-style-type: none;
}
/* text position */
.text-left {
  text-align: left !important;
}
.text-center {
  text-align: center !important;
}
.text-right {
  text-align: right !important;
}
.nowrap {
  white-space: nowrap !important;
}

/* loader */
.loaded .main-wrapper {
  opacity: 0;
}
.icon-load {
  background: url(../img/loader.gif) no-repeat left top;
  width: 40px;
  height: 40px;
  position: fixed;
  left: 50%;
  top: 50%;
  margin-left: -20px;
  margin-left: -20px;
  display: none;
}
.loaded .icon-load {
  display: block;
}

