          font-style: normal;
          font-weight: normal;
          font-size: 14px;
          line-height: 18px;
          color: #6e788d;
        }
        &:-ms-input-placeholder {
          font-family: "Muli", sans-serif;
          font-style: normal;
          font-weight: normal;
          font-size: 14px;
          line-height: 18px;
          color: #6e788d;
        }
        &::-moz-placeholder {
          font-family: "Muli", sans-serif;
          font-style: normal;
          font-weight: normal;
          font-size: 14px;
          line-height: 18px;
          color: #6e788d;
        } // Firefox 19+
        &:-moz-placeholder {
          font-family: "Muli", sans-serif;
          font-style: normal;
          font-weight: normal;
          font-size: 14px;
          line-height: 18px;
          color: #6e788d;
