.content__first-screen_form_container_stepper {
  display: flex;
  margin-top: 47px;

  .step {
    width: 140px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    white-space: nowrap;
    position: relative;

    p {
      font-style: normal;
      font-weight: normal;
      font-size: 14px;
      line-height: 18px;
      color: #ffffff;
      padding-top: 24px;
    }

