      font-family: "Muli", sans-serif;
      font-style: normal;
      font-weight: 900;
      font-size: 24px;
      line-height: 30px;
      color: #ffffff;
      position: absolute;
      left: 25px;
      top: -15px;
    }

    &:nth-of-type(1) {
      margin-right: 90px;
      position: relative;

      &::after {
        content: "";
        position: absolute;
        display: inline-block;
        width: 96px;
        height: 1px;
        background: #ffffff;
        right: -93px;
        bottom: 70%;
      }
    }
    &:nth-of-type(2) {
      margin-right: 90px;

      &::after {
        content: "";
        position: absolute;
        display: inline-block;
        width: 96px;
        height: 1px;
