.content__news__container-slider {
  max-width: 100%;
  height: 80%;
  display: -webkit-box;
  display: -webkit-flex;
  display: -moz-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: end;
  -webkit-align-items: flex-end;
  -moz-box-align: end;
  -ms-flex-align: end;
  align-items: flex-end;
}

.content__news__container-slider_item {
  h5 {
    margin: 0;
    width: 440px;
    padding-top: 18px;
    padding-left: 60px;
    padding-bottom: 15px;
    position: relative;

    &::before {
      content: "";
      width: 60px;
      height: 3px;
      background: $green;
      position: absolute;
      left: 0;
      top: 0;
    }
  }
}

.content__news__container-slider_item_container {
  width: 100%;
  display: -webkit-box;
  display: -webkit-flex;
  display: -moz-box;
  display: -ms-flexbox;
  display: flex;
  align-items: flex-start;
  justify-content: flex-end;
  position: relative;
}

.content__news__container-slider_item_container_right-box {
  position: relative;
}

.content__news__container-slider_item_container_right-box {
  img {
    height: auto;
    width: 579px;
  }
}

.content__news__container-slider_item_container_left-box {
  width: 50%;
  padding: 0 4%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;

  span {
    color: $green;
    width: 100%;
    padding: 40px 0;

    &::after {
      content: url("../img/ArrowGreen.svg");
      display: inline;
      padding-left: 10px;
    }
  }
}

.content__news__container-slider_item_container_left-box_content {
  width: 100%;
  height: 370px;
  padding: 6%;
  display: -webkit-box;
  display: -webkit-flex;
  display: -moz-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -webkit-flex-direction: column;
  -moz-box-orient: vertical;
  -moz-box-direction: normal;
  -ms-flex-direction: column;
  flex-direction: column;
  -webkit-box-align: center;
  -webkit-align-items: center;
  -moz-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  justify-content: flex-start;
  color: #9ca3ab;
}
.content__news__container-slider_item_container_left-box_content h3 {
  font-family: "Merriweather", serif;
  font-weight: 900;
  font-size: 20px;
}
.content__news__container-slider_item_container_left-box_content h3 span {
  font-weight: 500;
  font-style: italic;
  font-size: 15px;
}
.content__news__container-slider_item_container_left-box_content p {
  line-height: 1.5em;
  font-weight: Bold 900;
  letter-spacing: 0.32px;
}

.owl-dots {
  width: auto;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: -46px;
}
.owl-dots .owl-dot {
  display: inline-block;
  width: 6px;
  height: 6px;
  margin: 7px;
  background: #6e788d !important;
  border-radius: 50%;
}
.owl-dots .owl-dot.active {
  width: 21px;
  height: 21px;
  background: none !important;
  border: 1px solid #6e788d;
}

.owl-nav {
  display: -webkit-box;
  display: -webkit-flex;
  display: -moz-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -webkit-align-items: center;
  -moz-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  -webkit-justify-content: space-around;
  -ms-flex-pack: distribute;
  justify-content: space-around;
  width: 330px;
  height: 60px;
  margin: auto;

  span {
    display: none;
  }
}
.owl-nav .owl-prev {
  width: 50px;
  position: relative;
  display: flex;
  align-items: center;

  &::before {
    content: url("../img/ArrowLeft.svg");
  }
}

.owl-nav .owl-next {
  width: 50px;
  position: relative;
  display: flex;
  align-items: center;

  &::before {
    content: url("../img/ArrowRight.svg");
  }
}

/*
.owl-nav .owl-next:hover,
.owl-nav .owl-prev:hover {
  -webkit-transition-duration: 0.5s;
  -moz-transition-duration: 0.5s;
  -o-transition-duration: 0.5s;
  transition-duration: 0.5s;
  -webkit-transition-property: all;
  -o-transition-property: all;
  -moz-transition-property: all;
  transition-property: all;
  -webkit-transition-timing-function: ease-in-out;
  -moz-transition-timing-function: ease-in-out;
  -o-transition-timing-function: ease-in-out;
  transition-timing-function: ease-in-out;
}
*/
@media screen and (max-width: 1100px) {
  .content__news__container-slider_item_container {
    flex-direction: column-reverse;
    align-items: center;
  }

  .content__news__container-slider_item_container_right-box {
    width: 100%;
    display: flex;
    justify-content: center;
    padding: 0 60px;

    img {
      width: 100%;
    }
  }
  .content__news__container-slider_item_container_left-box {
    width: 100%;
    justify-content: center;
    padding: 0 60px;

    span {
      text-align: right;
    }
  }

  .content__news__container-slider_item_container_left-box_content {
    height: auto;
    padding-top: 20px;
  }
  .content__news {
    background-image: url("../img/flowerAdaptiveLeft.png");
  }
}

@media screen and (max-width: 767px) {
  .content__news__container-slider_item_container_left-box {
    width: 95%;
    padding: 0;
  }
  .content__news__container-slider_item_container_right-box {
    width: 95%;
    padding: 0;
  }

  .content__news__container-slider_item_container_left-box_content {
    padding: 20px 0;
  }

  .content__news__container-slider_item {
    h5 {
      padding-left: 30px;

      &::before {
        content: "";
        width: 30px;
      }
    }
  }
}
