/*
================================================================================
|                                     HEADER                                   |
================================================================================
*/
header {
  width: 100%;
  min-width: $min-width;
  text-align: left;
  order: 1;
  -webkit-order: 1;
  position: relative;
}

.header__wrapper {
  max-width: 1280px;
  margin: 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
}

.header__wrapper_container {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-flow: row wrap;
  background: #4b5464;

  .company {
    color: #fff;
  }
}

.header__wrapper_contacts {
  display: flex;
  margin-left: 65px;
  a {
    font-style: normal;
    font-weight: 600;
    font-size: 14px;
    line-height: 1.5;
    color: #ffffff;
    position: relative;
    margin-right: 36px;
    display: flex;
    align-items: center;

    &:hover {
      color: $green;
    }

    &:nth-of-type(1)::before {
      content: "";
      width: 16px;
      height: 16px;
      display: block;
      background: url("../img/phoneHead.svg");
      margin-right: 8px;
    }

    &:nth-of-type(2)::before {
      content: "";
      width: 16px;
      height: 16px;
      display: block;
      background: url("../img/mailHead.svg") center no-repeat;
      background-size: contain;
      margin-right: 8px;
    }
  }
}

.header__wrapper_registration {
  display: flex;
  align-items: center;
  margin-right: 65px;

  a {
    font-style: normal;
    font-weight: 600;
    font-size: 14px;
    line-height: 1.5;
    color: #ffffff;
    line-height: 54px;
    padding: 0 26px;
    display: flex;
    align-items: center;

    &:nth-of-type(1)::before {
      content: "";
      width: 30px;
      height: 30px;
      display: block;
      background: url("../img/Group 69.svg") center no-repeat;
      background-size: contain;
      margin-right: 8px;
    }

    &:nth-of-type(1) {
      &:hover {
        color: $green;
      }
    }

    &:nth-of-type(2) {
      background: $green;
    }
  }
}

.header__wrapper_adaptive-container {
  width: 60%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header__wrapper_container-nav {
  height: 35px;
  background: #ffffff;
  box-shadow: 0px 4px 25px rgba(5, 24, 92, 0.15);
  width: 100%;
  ul {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;

    li {
      height: 100%;
      padding: 0 2.45%;
      display: flex;
      align-items: center;
      a {
        font-style: normal;
        font-weight: 600;
        font-size: 14px;
        line-height: 1.5;
        text-align: center;
        color: #6e788d;
        width: fit-content;
        white-space: nowrap;

        &:hover {
          color: $green;
        }

        &.active {
          color: $green;
        }
      }
    }
  }
}

.burger-menu {
  width: 36px;
  height: 17px;
  display: none;
  flex-direction: column;
  justify-content: space-between;
  transition: all 0.4s linear;

  span {
    width: 100%;
    height: 3px;
    background: $green;
    transition: all 0.4s linear;
  }

  &.active {
    justify-content: center;
    bottom: 10px;
    transition: all 0.4s linear;

    span:nth-of-type(1) {
      transform: rotate(-405deg);
      transition: all 0.4s linear;
    }

    span:nth-of-type(2) {
      opacity: 0;
      margin-bottom: -6px;
      transition: all 0.2s linear;
    }

    span:nth-of-type(3) {
      transform: rotate(45deg);
      transition: all 0.4s linear;
    }
  }
}

@media screen and (max-width: 1150px) {
  .header__wrapper_container-nav {
    ul {
      li {
        padding: 0 1.5%;
      }
    }
  }

  .header__wrapper_registration {
    margin-right: 0;
  }

  .header__wrapper_contacts {
    margin-left: 30px;
  }

  .header__wrapper_adaptive-container {
    width: 52%;
  }
}

@media screen and (max-width: 991px) {
  .header__wrapper_adaptive-container {
    width: auto;
  }
  .header__wrapper_container-nav {
    position: absolute;
    top: 92px;
    z-index: 999;
    height: auto;
    display: none;

    ul {
      flex-direction: column;
      align-items: center;
      height: auto;
      background: #fff;
      line-height: 32px;

      a {
        padding: 5px 0;
      }
    }
  }
  .header__wrapper {
    padding-bottom: 38px;
  }

  .header__wrapper_adaptive-container {
    justify-content: flex-end;
  }

  .header__wrapper_container .company {
    position: absolute;
    bottom: 0;
    left: 30px;
    color: #6e788d;
  }

  .burger-menu {
    position: absolute;
    display: flex;
    right: 34px;
    bottom: 7px;
  }
}

@media screen and (max-width: 767px) {
  .header__wrapper_registration a {
    line-height: 41px;
  }

  .header__wrapper_container-nav {
    top: 79px;
  }
}

@media screen and (max-width: 650px) {
  .header__wrapper_registration {
    flex-direction: column-reverse;
  }

  .header__wrapper_contacts {
    height: 100%;
    flex-direction: column;
    align-self: flex-start;
    a {
      line-height: 39px;
    }
  }

  .header__wrapper_container-nav {
    top: 120px;
  }

  .header__wrapper_contacts {
    margin-left: 16px;

    a {
      margin-right: 0;
    }
  }
}
